import { Fragment, useState, useEffect } from "react";
import { connect } from "react-redux";
import "flatpickr/dist/themes/light.css";
import { RiCloseFill, RiInformationLine } from "react-icons/ri";
import { Button, Input, Form, Modal, Badge, Row, Col, Select } from "antd";

import NumericInput from "../../../components/data-entry/inputs/numberInput";
import {
  getMiniCounters,
  getPurchaseStock,
  setPurchaseStockParams
} from "../../../../store/mini/miniSlice";

import {
  reEstimationAdd,
  isModelVisible,
  InputChangeValue,
} from "../../../../store/Masters/ReEstimation/reEstimationSlice";

export const isObjEmpty = (obj) => Object.keys(obj).length === 0;

const AddRestimation = (props) => {
  const [form] = Form.useForm();
  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  // Adds New Country
  const handleAdd = () => {
    const obj = {
      id: 0,
      amount: props.rowdata?.amount,
      description: props.rowdata.description,
      counter_id: props.rowdata.counter_id,
      purchase_id: props.rowdata.purchase_id,
    };
      props.reEstimationAdd(obj);
  };
  // Updates Country in Store
  const handleUpdate = () => {
    const obj = {
      id: props.rowdata?.id,
      amount: props.rowdata?.amount,
      description: props.rowdata?.description,
      counter_id:
        props.rowdata?.counter_id == null
          ? props.rowdata.counter.id
          : props.rowdata?.counter_id,
      purchase_id:
          props.rowdata?.purchase_id == null
            ? props.rowdata.purchase.id
            : props.rowdata?.purchase_id,
    };
    props.reEstimationAdd(obj);
  };

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isModelVisible(false);
  };

  useEffect(() => {
    props.getMiniCounters();
  }, []);

  const FooterAddOrUpdate = () => {
    if (props.rowdata.id == null || props.rowdata.id == 0) {
      return (
        <Fragment>
          <Button
            onClick={() => {
              form.submit();
            }}
            type="primary"
            block
          >
            Add
          </Button>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Button
            type="primary"
            onClick={() => {
              form.submit();
            }}
            block
          >
            Update
          </Button>
        </Fragment>
      );
    }
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      {props.rowdata.id == null || props.rowdata.id == 0 ? "Add" : "Update"}{" "}
      Re-Estimation
    </h5>
  );

  const onSearch = (value) => {
    console.log("search:", value);
  };

  function checkAmount(data) {
    if (parseInt(data.value) > 0 && parseInt(data.value) <= 99) {
      props.InputChangeValue({
        key: data.key,
        value: "",
      });
      Modal.error({
        icon: (
          <span className="remix-icon">
            <RiInformationLine />
          </span>
        ),
        title: (
          <h5 className="hp-mb-0 hp-font-weight-500">Amount Validation</h5>
        ),
        content: (
          <div>
            <p className="hp-p1-body hp-text-color-black-80">
            Amount must be minimum 3 Digits
            </p>
          </div>
        ),
      });
    } else {
    }
  }

  useEffect(() => {
    form.setFieldsValue({
      counter_id: props.rowdata.counter_id,
      purchase_id: props.rowdata.purchase_id,
      amount: props.rowdata.amount,
      description: props.rowdata.description,
    });
  }, [props.rowdata]);

  return (
    <Modal
      visible={props.model}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        form.resetFields();
        props.isModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={616}
    >
      <Form
        layout="vertical"
        form={form}
        name="userForm"
        autoComplete="off"
        onFinish={() => {
          if (props.rowdata.id == null) {
            handleAdd();
          } else {
            handleUpdate();
          }
        }}
      >
        <Row gutter={[12, 0]}>
          <Col span={24} xs={24} xl={12} md={12}>
            <Form.Item
              label="Counter"
              name="counter_id"
              rules={[
                {
                  required: true,
                  message: "Please Select Counter",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                placeholder="Select a counter"
                optionFilterProp="counter"
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                onBlur={(value) => {
                    props.getPurchaseStock({
                      ...props.purchaseStockParams,
                      counter: props.rowdata.counter_id,
                      page: 1,
                    });
                    props.setPurchaseStockParams({
                      ...props.purchaseStockParams,
                      counter: props.rowdata.counter_id,
                      page: 1,
                    });
                }}
                onChange={(value) => {
                  props.InputChangeValue({
                    key: "counter_id",
                    value: value,
                  });
                }}
                value={
                  props.rowdata.counter_id == null
                    ? ""
                    : props.rowdata?.counter_id
                }
              >
                {props.miniCountersList.map((counter, index) => {
                  return (
                    <Option key={index} value={counter.id}>
                      {counter.name}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>

          <Col span={24} xs={24} xl={12} md={12}>
            {props.rowdata.id == null ? (
            <Form.Item
              label="Purchase"
              name="purchase_id"
              rules={[
                {
                  required: true,
                  message: "Please Select Purchase",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                placeholder="Select a Purchase"
                optionFilterProp="Purchase"
                filterOption={(input, option) =>
                  (option.children + "")
                    .toLowerCase()
                    .includes(input.toLowerCase())
                }
                disabled={props.rowdata.id == null ? false : true}
                onSearch={(value) => {
                  props.setPurchaseStockParams({
                    ...props.purchaseStockParams,
                    page: 1,
                    search: value,
                  });
                  props.getPurchaseStock({
                    ...props.purchaseStockParams,
                    page: 1,
                    search: value,
                  });
                }}
                // onBlur={(value) => {
                //   if (props.minSaleAmount == 0) {
                //     getMaxSaleAmount({
                //       key: "purchaserate",
                //       value: props.rowdata?.purchaserate,
                //     });
                //     getMinSaleAmount({
                //       key: "purchaserate",
                //       value: props.rowdata?.purchaserate,
                //     });
                //   } else {
                //     getMaxSaleAmountInMinSale({
                //       key: "minSaleAmount",
                //       value: props.minSaleAmount,
                //     });
                //   }
                // }}
                onChange={(value) => {
                  // vehicleData(value);
                  props.InputChangeValue({
                    key: "purchase_id",
                    value: value,
                  });
                }}
                value={
                  props.rowdata.purchase_id == null
                    ? ""
                    : props.rowdata?.purchase_id
                }
                onPopupScroll={() => {
                  if (
                    props.purchaseStockParams.page <
                    props.purchaseStockParams.no_of_pages
                  ) {
                    props.setPurchaseStockParams({
                      ...props.purchaseStockParams,
                      page: props.purchaseStockParams.page + 1,
                    });
                    props.getPurchaseStock({
                      ...props.purchaseStockParams,
                      page: props.purchaseStockParams.page + 1,
                    });
                  }
                }}
              >
                {props.purchaseStockList.map((purchase, index) => {
                  return (
                    <Option key={index} value={purchase.id}>
                      {purchase.vehicle?.vehicleno} - {purchase.code}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
            ) : (
              <Form.Item label="Purchase">
                  <Input
                    id="purchase_id"
                    name="purchase_id"
                    style={{ width: "100%" }}
                    value={props.rowDataView?.purchase?.code}
                    disabled
                  />
                </Form.Item>
            ) }
          </Col>

          <Col span={24} xs={24} xl={12} md={12}>
            <Form.Item
              label="Amount :"
              name="amount"
              rules={[
                {
                  required: true,
                  message: "Please input your Amount!",
                },
              ]}
              getValueProps={() => {}}
              hasFeedback
            >
              <NumericInput
                id="amount"
                name="amount"
                style={{ width: "100%" }}
                placeholder="Amount"
                maxLength={6}
                onBlur={(value) => {
                  checkAmount(value);
                }}
                value={
                  props.rowdata?.amount == null ? "" : props.rowdata?.amount
                }
                onChange={props.InputChangeValue}
              />
            </Form.Item>
          </Col>
          <Col md={24} xs={24}>
            <Form.Item label="Description :" name="description">
              <Input.TextArea
                id="description"
                name="description"
                style={{ width: "100%" }}
                placeholder="Description"
                value={props.rowdata?.description}
                onChange={ChangeHandeler}
              />
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ reEstimation, mini }) {
  return {
    rowdata: reEstimation.rowdata,
    model: reEstimation.model,
    miniCountersList: mini.miniCountersList,
    purchaseStockList: mini.purchaseStockList,
    purchaseStockParams: mini.purchaseStockParams,
    rowDataView: reEstimation.rowDataView
  };
}

export default connect(mapStateToProps, {
  reEstimationAdd,
  isModelVisible,
  InputChangeValue,
  getMiniCounters,
  getPurchaseStock,
  setPurchaseStockParams
})(AddRestimation);
